:root {
  font-size: 15px; }
  @media screen and (max-width: 768px) {
    :root {
      font-size: 14px; } }

body {
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  font-weight: 300;
  line-height: 1.8;
  color: #0d113f; }

p {
  margin-bottom: 1.25rem; }

h1,
h2,
h3,
h4,
h5 {
  margin: 2.75rem 0 1rem;
  font-weight: 400;
  line-height: 1.35; }

h1 {
  margin-top: 0; }

h1,
.display-1 {
  font-size: 2.488rem; }

h2,
.display-2 {
  font-size: 2.074rem; }

h3,
.display-3 {
  font-size: 1.728rem; }

h4,
.display-4 {
  font-size: 1.44rem; }

h5,
.display-5 {
  font-size: 1.2rem; }

small,
.text_small {
  font-size: 0.8rem; }

.container {
  width: 100%;
  position: relative;
  max-width: 1920px;
  margin: 0 auto; }

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

ul,
li {
  list-style: none; }

.fixed {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10; }

.wrapper {
  width: 100%;
  height: 100%;
  position: relative; }

.hoverable {
  transition: box-shadow 0.25s; }
  .hoverable:hover {
    box-shadow: -3.125px 3.125px 6.25px 1.5px rgba(0, 0, 0, 0.15); }

.btn {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  display: inline-block;
  font-weight: 500;
  text-transform: capitalize;
  padding: 0.75rem 2.5rem;
  margin: 2px 1rem;
  color: white;
  border-radius: 2px;
  letter-spacing: 1px;
  background-color: black; }
  .btn.btn-block {
    width: 100%; }
  .btn .btn-icon,
  .btn i.btn-icon {
    font-size: 32px; }
